<template>

<div class="container mx-auto mt-5">
    <div class="card text-cente">
      <div class="card-header">
        <ul class="nav nav-tabs card-header-tabs">
          <li class="nav-item">
            <a
              v-on:click="valor = 0"
              class="nav-link"
              v-bind:class="listar()"
              aria-current="true"
              href="#"
              >Listado</a
            >
          </li>
          <div v-if="acceso==2">
            <li class="nav-item">
            <a
              v-on:click="valor = -1"
              class="nav-link"
              v-bind:class="crear()"
              href="#"
              name="1"
              >Nuevo</a
            >
          </li>
        </div>
        <div v-if="acceso==2">
          <li class="nav-item">
            <a v-if="valor>0"
             
              class="nav-link active"
              href="#"
              >Editar</a
            >
            <a v-else
           
              class="nav-link disabled"
              href="#"
              >Editar</a
            >
          </li>
        </div>
          
        </ul>
      </div>
      <div class="card-body bg-white">
        <!-- <CrearLibro v-show="valor==1" variable=1></CrearLibro>
       <CrearLibro v-show="valor==2" variable=2></CrearLibro> -->

        
    <CrearLibro
      @recibiendo="variableLibro"
      v-show="valor < 0"
      v-bind:variable="valor"
    ></CrearLibro>
    <CrearLibro
      @recibiendo="variableLibro"
      v-if="valor > 0"
      v-bind:variable="valor"
    ></CrearLibro>
    <ListarLibro v-show="valor == 0"
      @recibiendo="variableLibro"
      v-if="valor == 0"
      v-bind:variable="valor"
      class="mx-2 mx-sm-5"
    >
    </ListarLibro>


      </div>
    </div>
  </div>







</template>

<style></style>

<script>
// @ is an alias to /src
import ListarLibro from "@/components/ListarLibro.vue";
import CrearLibro from "@/components/CrearLibro.vue";

// import Redes from "@/components/footeraComponent.vue";
//import Vue from 'vue';

export default {
  name: "LibrosView",

  data() {
    return {
      valor: 0,
      acceso: localStorage.getItem("acceso"),
      obtenerLibros: [],
    };
  },
  components: {
    ListarLibro,
    CrearLibro,


    // Redes,
  },

  mounted: function () {
   
    if (localStorage.getItem("acceso") ==0) {
      this.$router.push({ name: "home" });
    }

  },
  

  methods: {
    
    variableLibro(value) {
      this.valor = value;
    },
   
    listar() {
      return this.valor === 0 ? "active" : "";
    },
    crear() {
      return this.valor === -1 ? "active" : "";
    },
    editar() {
      return this.valor === 1 ? "active" : "";
    },
  
  },
};
</script>

