<template>

   <div class="row modalCompleto">
     <img class="col-6" v-bind:src="''+Libro.urlPortada"/>
     <div class="col-6"> 
       <div>
         
         <label
         >Libro solicitado: {{ Libro.nombreLibro }}. <br />
         Autor: {{ Libro.autor }}. <br />Editorial:
         {{ Libro.editorial }}.<br />
         Edición: {{ Libro.edicion }}</label
         >
        </div>
        
        <hr>
        <div class="form-group">
          
          
          
          
          <div v-if="idAdmin==0 || idAdmin==null ||  idAdmin==undefined ">
            <div>
              <label for=""
              >Nombre Completo: {{ usuario.nombre }} {{ usuario.apellidoP }}
              {{ usuario.apellidoM }}</label
              >
            </div>
            <div class="row">
              <div class="col-6">
                <label for="">Codigo de alumno</label>
                <input
                type="text"
                class="form-control"
                name="codigoUsuario"
                id=""
                v-bind:value="usuario.cod_alum"
                aria-describedby="helpId"
                placeholder=""
                readonly
                />
              </div>
              <div class="col-6">
                <label for="">Ci/Pasaporte</label>
                <input
                type="password"
                class="form-control"
                name="codigoUsuario"
                id=""
                v-bind:value="usuario.ci_pasaporte"
                aria-describedby="helpId"
                placeholder=""
                readonly
                />
              </div>
            </div>
            <br />
            
            <div>
              <label for="">Cantidad</label>
              <input
              type="number"
              class="form-control"
              name="codigoUsuario"
              id=""
              v-bind:value="1"
              aria-describedby="helpId"
              placeholder=""
              
              />
            </div>
          </div>
          <div v-else>
            <div>
              <label for=""
              >Nombre Completo: {{ nombre }} {{ apellidoP }}
              {{ apellidoM }}</label
              >
            </div>
            <div class="row">
              <div class="col">
                <label for="">Codigo de alumno</label>
                <input
                type="text"
                class="form-control"
                name="codigoUsuario"
                id=""
                v-on:keyup="buscarAlumno()"
                v-model="cod_alum"
                aria-describedby="helpId"
                placeholder=""
                
                />
              </div>
              
            </div>
            <br />
            
            <div>
              <label for="">Cantidad</label>
              <input
              type="number"
              class="form-control"
              name="codigoUsuario"
              id=""
              v-bind:value="1"
              aria-describedby="helpId"
              placeholder=""
              
              />
            </div>
          </div>
          
          
          
          
          <div>
            <label for="">Fecha para recoger el Libro</label>
            <input
            type="date"
            v-model="fechas.inicio"
            class="form-control"
            aria-describedby="helpId"
            placeholder=""
            required
            />
          </div>
          <div>
            <label for="">Fecha para entregar libro</label>
            <input
            type="date"
            v-model="fechas.fin"
            class="form-control"
            aria-describedby="helpId"
            placeholder=""
            required
            />
          </div>
          <div >
            <label for="">Numero de whatsapp</label>
            <br>
            <input class="col-12"
            type="number"
            aria-describedby="helpId"
            placeholder="Notificara la confirmacion de la solicitud"
            v-model="telefono"
            />
          </div>
          <br>
        </div>
        <div v-if="idAdmin>0">
          <button class="col-12" @click="VerificarAlumno(Libro.id)"  id="solicitar" :disabled='desabilitado'
          v-bind:style="{ backgroundColor: colorBoton}" >{{botonTexto}}</button>
        </div>
        <div v-else>
          <button class="col-12 btn-primary
          " @click="VerificarLogin(Libro.id)" id="solicitar" :disabled='desabilitado'
          v-bind:style="{ backgroundColor: colorBoton}"
          >{{botonTexto}}</button>
        </div>
      </div>
    </div>
  

  </template>

<script>
import axios from "axios";
export const RUTA_SERVIDOR_ALUMNOS = process.env.VUE_APP_RUTA_API_ALUMNOS;
export const RUTA_SERVIDOR_LIBROS = process.env.VUE_APP_RUTA_API_LIBROS;


export default {
  
  data() {
    return {
     
      colorBoton:'#56CCFF',
      botonTexto:'Solicitar Libro',
      desabilitado:false,
      
      idAdmin:localStorage.getItem('id'),
      usuario: {
        cod_alum: localStorage.getItem("cod_alum"),
        nombre: localStorage.getItem("nombre"),
        apellidoP: localStorage.getItem("ap_paterno"),
        apellidoM: localStorage.getItem("ap_materno"),
        ci_pasaporte: localStorage.getItem("ci_pasaporte"),
      },
      
        cod_alum: '',
        nombre: '',
        apellidoP: '',
        apellidoM: '',

      fechas: {
        inicio: "",
        fin: "",
      },
      telefono:null
    };
  },

  props: {
    Libro: {},
  },

  methods: {
    prestamo(libro) {
      // ver si hay libro disponible
      fetch(RUTA_SERVIDOR_LIBROS+"/libros/" + libro)
        .then((respuesta) => respuesta.json())
        .then((datosRespuesta) => {
          if (datosRespuesta.copiasDisponibles > 0) {
            
            axios
              .post(RUTA_SERVIDOR_LIBROS+"/prestamos/", {
                idEmpleado: parseInt(localStorage.getItem('id')),
                codigoUsuario: localStorage.getItem("cod_alum"),
                idLibro: libro,
                cantidad: 1,
                fechaPrestamo: this.fechas.inicio,
                fechaDevolucion: this.fechas.fin,
                observacion: "null",
                estado: 1,
                activo: 1,
                telefono: this.telefono,
                idSubsede: localStorage.getItem('idSubsede')
              })
              .then(function (response) {
               
                console.log(response);
              
                
              },
              this.botonTexto='Solicitud enviada',
              this.colorBoton='#00D12F'
              )
              
            
              .catch(function (error) {
                console.log(error);
              });
          } else {
            alert("No hay copias disponibles");
          }
        }
        )
        .catch(console.log);
    },

    VerificarLogin(libro) {
   
      fetch(
        RUTA_SERVIDOR_ALUMNOS+"/alumno/" +
          localStorage.getItem("cod_alum") +
          "/" +
          localStorage.getItem("ci_pasaporte")
      )
        .then((respuesta) => respuesta.json())
        .then((datosRespuesta) => {
          
          if (datosRespuesta != "") {
            var fechaActual = new Date().getTime();
            console.log(fechaActual);
            var fechaInicio = new Date(this.fechas.inicio).getTime();

            var fechaFin = new Date(this.fechas.fin).getTime();
            console.log(fechaInicio);
            var tiempo = (fechaFin - fechaInicio) / (1000 * 60 * 60 * 24);
            if (fechaInicio >= fechaActual) {
              if (tiempo < 15 && tiempo >= 0) {
                this.desabilitado=true;
                this.botonTexto='Solicitando Libro'
                this.prestamo(libro);
                
              } else {
                alert("El tiempo del préstamo debe ser entre 1 y 15 días");
              }
            } else {
              alert("La fecha debe ser mayor o igual a la fecha actual");
            }
          }
        })
        .catch(function () {
          localStorage.setItem("acceso", 0);
          localStorage.setItem("cod_alum", "");
          localStorage.setItem("ci_pasaporte", "");
          localStorage.setItem("nombre", "");
          localStorage.setItem("ap_paterno", "");
          localStorage.setItem("ap_materno", "");
          localStorage.setItem("sede", "");
          localStorage.setItem("id", 0);
          location.reload();
          this.$router.push("/");
        });
    },


    

    VerificarAlumno(libro) {
      this.elemento= document.getElementById('solicitar');
      fetch(
        RUTA_SERVIDOR_ALUMNOS+"/alumno/" +
          this.cod_alum
      )
        .then((respuesta) => respuesta.json())
        .then((datosRespuesta) => {
          if (datosRespuesta != "") {
            var fechaActual = new Date().getTime();
            console.log(fechaActual);
            var fechaInicio = new Date(this.fechas.inicio).getTime();

            var fechaFin = new Date(this.fechas.fin).getTime();
            console.log(fechaInicio);
            var tiempo = (fechaFin - fechaInicio) / (1000 * 60 * 60 * 24);
            if (fechaInicio >= fechaActual) {
              if (tiempo < 15 && tiempo >= 0) {
                this.desabilitado=true;
                this.botonTexto='Solicitando Libro'
                this.prestamo(libro);
              } else {
                alert("El tiempo del préstamo debe ser entre 1 y 15 días");
              }
            } else {
              alert("La fecha debe ser mayor o igual a la fecha actual");
            }
          }
        })
        .catch(function () {
          alert("Existen datos incorrectos");
        });
    },




    buscarAlumno(){
   
     fetch(RUTA_SERVIDOR_ALUMNOS+"/alumno/" + this.cod_alum)
        .then((respuesta) => respuesta.json())
        .then((datosRespuesta) => {
          this.nombre=datosRespuesta.nombre;
          this.apellidop=datosRespuesta.ap_paterno;
          this.apellidoM=datosRespuesta.ap_materno;
          localStorage.setItem("cod_alum",datosRespuesta.cod_alum)

        })
        .catch(this.nombre='Usuario no encontrado',
          this.apellidop='',
          this.apellidoM='',
          localStorage.setItem("cod_alum",'')
         ).catch(console.clear );
    }





  },
};
</script>

