<template>
  <div class="p-5">
    <div class="form-group" align="center">
      <label>Seleccione el estado del prestamo</label>
      <select
        v-model="estado"
        @change="filtrarSolicitudes()"
        class="custom-select"
        name=""
        id=""
      >
        <option value="-1">Todos los estados</option>
        <option value="0">Solicitud rechazada</option>
        <option value="1">Pendiente de respuesta</option>
        <option value="2">Solicitud aceptada</option>
        <option value="3">libro devuelto</option>
        <option value="4">Pendiente de devolucion</option>
        <option value="5">Retrasado</option>
      </select>

    <br>
    <label>Seleccione el rango de fechas</label><br />
    <b-row cols="2">
      
      <b-col>

       <input
        type="date"
        v-model="fechaInicio"
        @change="filtrarSolicitudes()"
        class="form-control"
        name=""
        id=""
      />
    </b-col>
      <b-col>
         <input
        type="date"
        v-model="fechaFin"
        @change="filtrarSolicitudes()"
        class="form-control"
        name=""
        id=""
      />
 
   </b-col>

  </b-row>
      

      <br>
    </div>
    <table class="table table-striped border text-center">
      <thead>
        <tr>
          <th>Solicitante</th>
          <th>Libro solicitado</th>
          <th>cantidad</th>
          <th>Fecha de prestamo</th>
          <th>Fecha de devolucion</th>
          <th>estado</th>
          <th>Acción</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="solicitud in datosPaginados" :key="solicitud.id">
          <td>{{NombreSolicitate[solicitud.id]}} </td>
          <td>{{ solicitud.titulo }}</td>

          <td>{{ solicitud.cantidad }}</td>
          <td>{{ solicitud.fechaPrestamo }}</td>
          <td>{{ solicitud.fechaDevolucion }}</td>
          <td>
            <p v-if="solicitud.estado == 0">
              Solicitud rechazada
            </p>
            <p v-if="solicitud.estado == 1">Pendiente de respuesta</p>
            <p v-if="solicitud.estado == 2">
              Solicitud aceptada
            </p>
            <p v-if="solicitud.estado == 3">libro devuelto</p>
            <p v-if="solicitud.estado == 4">Pendiente de devolucion</p>
            <p v-if="solicitud.estado == 5">Retrasado</p>
          </td>
          <td>
            <a v-if="solicitud.estado == 1 ||  solicitud.estado == 2"
              @click="eliminarSolicitud(solicitud,NombreSolicitate[solicitud.id])"
              class="btn btn-danger"
              role="button"
              >👎🏻</a
            >
            <a v-if="solicitud.estado == 0 || solicitud.estado == 1"
              @click="aceptarSolicitud(solicitud,NombreSolicitate[solicitud.id])"
              class="btn btn-success"
              role="button"
              >
              👍🏻
              </a
            >
            
            <a v-if=" solicitud.estado == 2 "
              @click="entregarLibro(solicitud)"
              class="btn btn-warning"
              role="button"
              >📦</a
            >
             <a v-if="solicitud.estado == 4 || solicitud.estado == 5 "
              @click="devolverLibro(solicitud)"
              class="btn btn-info"
              role="button"
              >🔙</a
            >
             <a v-if="solicitud.estado != 0 "
              @click="imprimirSolicitud(solicitud,NombreSolicitate[solicitud.id])"
              class="btn btn-primary"
              role="button"
              >🖨️</a
            >
          </td>
          
        </tr>
      </tbody>
    </table>
    <nav aria-label="Page navigation example">
      <ul align="center" class="pagination">
        <li class="page-item" v-on:click="paginaPrevia()">
          <a class="page-link" href="#">Previous</a>
        </li>
        <li
          v-for="pagina in totalPaginas()"
          :key="pagina"
          class="page-item"
          v-bind:class="activo(pagina)"
        >
          <a class="page-link" href="#" v-on:click="ObtenerDatosPag(pagina)">{{
            pagina
          }}</a>
        </li>
        <li class="page-item" v-on:click="paginaSiguiente()">
          <a class="page-link" href="#">Next</a>
        </li>
      </ul>
    </nav>



  <table id="solicitudesTabla" style="display:none;">
      <thead>
        <tr>
          <th>Solicitante</th>
          <th>Libro solicitado</th>
          <th>cantidad</th>
          <th>Fecha de prestamo</th>
          <th>Fecha de devolucion</th>
          <th>estado</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="solicitud in solicitudesFiltradas" :key="solicitud.id">
          <td>{{NombreSolicitate[solicitud.id]}} </td>
          <td>{{ solicitud.titulo }}</td>

          <td>{{ solicitud.cantidad }}</td>
          <td>{{ solicitud.fechaPrestamo }}</td>
          <td>{{ solicitud.fechaDevolucion }}</td>
          <td>
            <p v-if="solicitud.estado == 0">
              Solicitud rechazada
            </p>
            <p v-if="solicitud.estado == 1">Pendiente de respuesta</p>
            <p v-if="solicitud.estado == 2">
              Solicitud aceptada
            </p>
            <p v-if="solicitud.estado == 3">libro devuelto</p>
            <p v-if="solicitud.estado == 4">Pendiente de devolucion</p>
          </td>
          
          
        </tr>
      </tbody>
    </table>
   
    <button
       
        type="button"
        class="btn btn-primary"
        @click="imprimirSolicitudes()"
      > Imprimir reportes 🖨️</button>







  </div>
  
</template>

<script>
import axios from "axios";
export const RUTA_SERVIDOR_ALUMNOS = process.env.VUE_APP_RUTA_API_ALUMNOS;
export const RUTA_SERVIDOR_LIBROS = process.env.VUE_APP_RUTA_API_LIBROS;

//import * as saveAs from 'file-saver';

import { jsPDF, } from 'jspdf';
import  'jspdf-autotable';
export default {
  data() {
    return {
      fechaInicio: "",
      fechaFin: "",
      estado: -1,
      solicitudes: [],
      libros: [],
      preTitulo: {},
      
      solicitudesFiltradas: [],
      elementosPorPagina: 8,
      datosPaginados: [],
      paginaActual: 1,
      NombreSolicitate:[],
    };
  },
  mounted() {
    this.obtenersolicitudes();
    
  },


  methods: {

    imprimirSolicitud(solicitud,nombre) {
      //crear archivo pdf
      var doc = new jsPDF();
      doc.setFontSize(20);
      doc.text(20, 20, "Solicitud de prestamo");
      //colocar logo
      doc.addImage
       doc.text(180, 20, "UNO");
      doc.setFontSize(12);
      doc.text(20, 30, "Nombre: " + nombre);
       doc.text(20, 40, "Codigo de usuario: " + solicitud.codigoUsuario);
      doc.text(20, 50, "Libro: " + solicitud.titulo);
      doc.text(20, 60, "Cantidad: " + solicitud.cantidad);
      doc.text(20, 70, "Fecha de prestamo: " + solicitud.fechaPrestamo);
      doc.text(20, 80, "Fecha de devolucion: " + solicitud.fechaDevolucion);
      doc.text(20, 100, "Firma del alumno:______________________" );
      doc.text(0, 140, "----------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------" );
    

      doc.setFontSize(20);
      doc.text(20, 170, "Solicitud de prestamo");
      doc.text(180, 170, "UNO");
      doc.setFontSize(12);
      doc.text(20, 180, "Nombre: " + nombre);
       doc.text(20, 190, "Codigo de usuario: " + solicitud.codigoUsuario);
      doc.text(20, 200, "Libro: " + solicitud.titulo);
      doc.text(20, 210, "Cantidad: " + solicitud.cantidad);
      doc.text(20, 220, "Fecha de prestamo: " + solicitud.fechaPrestamo);
      doc.text(20, 230, "Fecha de devolucion: " + solicitud.fechaDevolucion);
      doc.text(20, 250, "Firma del alumno:______________________" );
      // doc.text(20, 90, "Observacion: " + solicitud.observacion);
      // doc.text(20, 100, "Estado: " + solicitud.estado);
      // doc.text(20, 110, "Subsede: " + solicitud.subsede);
      // doc.text(20, 120, "Telefono: " + solicitud.telefono);
      doc.save("solicitud.pdf");

    },



    obtenersolicitudes() {
      fetch(RUTA_SERVIDOR_LIBROS+"/prestamo/"+localStorage.getItem('idSubsede'))
        .then((respuesta) => respuesta.json())
        .then((datosRespuesta) => {
          this.solicitudes = datosRespuesta.reverse();})
        .then(() => {
           for (let index = 0; index < this.solicitudes.length; index++) {
       this.asignarNombre(this.solicitudes[index].codigoUsuario,this.solicitudes[index].id)
       
      }

      
        })
        .catch(console.log);
    },


     asignarNombre(codigoAlumnon,index){
      if(this.NombreSolicitate[index]==undefined){
      fetch(RUTA_SERVIDOR_ALUMNOS+"/alumno/" + codigoAlumnon)
      .then((respuesta) => respuesta.json())
      .then((datosRespuesta) => {
        this.filtrarSolicitudes();
        this.NombreSolicitate[index]=(datosRespuesta.nombre + " " + datosRespuesta.ap_paterno + " " + datosRespuesta.ap_materno);
      })
      }
      else{
      return 
      }
    },

    filtrarSolicitudes() {
      if (this.estado == -1 ) {
        console.log(this.fechaFin,this.fechaInicio);
        this.solicitudesFiltradas = this.solicitudes;
        this.ObtenerDatosPag(1);
      } else {
        this.solicitudesFiltradas = [];
        for (let index = 0; index < this.solicitudes.length; index++) {
          if(this.fechaInicio!="" && this.fechaFin!=""){

          if (this.solicitudes[index].estado === parseInt(this.estado)
            &&  (this.solicitudes[index].fechaPrestamo >= this.fechaInicio
            &&  this.solicitudes[index].fechaPrestamo <= this.fechaFin)) {
          
            this.solicitudesFiltradas.push(this.solicitudes[index]);
            this.ObtenerDatosPag(1);
          }
          }
          else{
            if (this.solicitudes[index].estado === parseInt(this.estado)) {
              console.log(this.solicitudes[index].fechaPrestamo);
            this.solicitudesFiltradas.push(this.solicitudes[index]);
            this.ObtenerDatosPag(1);
          }
          }
          
        }
        if (this.solicitudesFiltradas.length === 0) {
          this.paginaPrevia();
        }
      }
    },
    totalPaginas() {
      return Math.ceil(
        this.solicitudesFiltradas.length / this.elementosPorPagina
      );
    },
    ObtenerDatosPag(noPag) {
      this.paginaActual = noPag;
      this.datosPaginados = [];
      let ini = noPag * this.elementosPorPagina - this.elementosPorPagina;
      let fin = noPag * this.elementosPorPagina;
      this.datosPaginados = this.solicitudesFiltradas.slice(ini, fin);
  
    },


    paginaPrevia() {
      if (this.paginaActual > 1) {
        this.paginaActual--;
      }
      this.ObtenerDatosPag(this.paginaActual);
    },
    paginaSiguiente() {
      if (this.paginaActual < this.totalPaginas()) {
        this.paginaActual++;
      }
      this.ObtenerDatosPag(this.paginaActual);
    },
    activo(noPoag) {
      return noPoag === this.paginaActual ? "active" : "";
    },

    eliminarSolicitud(solicitud,nombre) {
  var valor;
  if(solicitud.estado==2){
    valor=1;
  }
  else{
    valor=0;
  }
      
     try {
      axios
        .put(RUTA_SERVIDOR_LIBROS+"/prestamos/" + solicitud.id, {
          idEmpleado: solicitud.idEmpleado,
          codigoUsuario: solicitud.codigoUsuario,
          idLibro: solicitud.idLibro,
          cantidad: solicitud.cantidad,
          fechaPrestamo: solicitud.fechaPrestamo,
          idSubsede:solicitud.idSubsede,
          fechaDevolucion: solicitud.fechaDevolucion,
          observacion: "Prestamo rechazado por el bibliotecario",
          estado: 0,
          activo: 1,
          telefono:solicitud.telefono,
        })
      .then(function () {


            fetch(RUTA_SERVIDOR_LIBROS+"/libros/"+ solicitud.idLibro)
        .then((respuesta) => respuesta.json())
        .then((datosRespuesta) => {
          {
            
            axios
                  .put(RUTA_SERVIDOR_LIBROS+"/libros/" + solicitud.idLibro, {
                    titulo:datosRespuesta.titulo,
                    copias: datosRespuesta.copias,
                    
                    copiasDisponibles: datosRespuesta.copiasDisponibles + valor,
                    categoria: datosRespuesta.categoria,
                    edicion: datosRespuesta.edicion,
                    codigo: datosRespuesta.codigo,
                    idAutor: parseInt(datosRespuesta.idAutor),
                    idEmpleado: datosRespuesta.idEmpleado,
                    idEditorial: parseInt(datosRespuesta.idEditorial),
                    idCarrera: parseInt(datosRespuesta.idCarrera),
                    urlPortada: datosRespuesta.urlPortada,
                    observacion: datosRespuesta.observacion,
                    activo: 1,
                  })
                  .then(function () {
                    if(solicitud.telefono!=null){
          window.open ('https://api.whatsapp.com/send?phone=591'+solicitud.telefono+'&text=Hola '+nombre+', nos comunicamos desde la Universidad Nacional del Oriente sede '+localStorage.getItem("Subsede") +', para informarte que tu solicitus del libro "'+solicitud.titulo+'" a sido rechazada, lamentamos este inconveniente y te pedimos que puedas pasar personalmente por la universidad a realizar la solicitud'); 
        }
        })
        .then(function () {
          location.reload();
          
        })

                  .catch(function (error) {
                    console.log(error);
                  
                  });

          }
        })
        .catch(console.log);
  
          
    }
        )}

        catch( error) {
          console.log(error);
          // console.log(this.libro);
        }
    },

    aceptarSolicitud(solicitud,nombre) {
    
   
    try {
      axios
        .put(RUTA_SERVIDOR_LIBROS+"/prestamos/" + solicitud.id, {
          idEmpleado: solicitud.idEmpleado,
          codigoUsuario: solicitud.codigoUsuario,
          idLibro: solicitud.idLibro,
          cantidad: solicitud.cantidad,
          fechaPrestamo: solicitud.fechaPrestamo,
          idSubsede:solicitud.idSubsede,
          fechaDevolucion: solicitud.fechaDevolucion,
          observacion: "Prestamo aceptado por el bibliotecario",
          estado: 2,
          activo: 1,
          telefono:solicitud.telefono,
        })
        .then(function () {


            fetch(RUTA_SERVIDOR_LIBROS+"/libros/"+ solicitud.idLibro)
        .then((respuesta) => respuesta.json())
        .then((datosRespuesta) => {
          {
            axios
                  .put(RUTA_SERVIDOR_LIBROS+"/libros/" + solicitud.idLibro, {
                    titulo:datosRespuesta.titulo,
                    copias: datosRespuesta.copias,
                    copiasDisponibles: datosRespuesta.copiasDisponibles - 1,
                    categoria: datosRespuesta.categoria,
                    edicion: datosRespuesta.edicion,
                    codigo: datosRespuesta.codigo,
                    idAutor: parseInt(datosRespuesta.idAutor),
                    idEmpleado: datosRespuesta.idEmpleado,
                    idEditorial: parseInt(datosRespuesta.idEditorial),
                    idCarrera: parseInt(datosRespuesta.idCarrera),
                    urlPortada: datosRespuesta.urlPortada,
                    observacion: datosRespuesta.observacion,
                    activo: 1,
                  })
                  .then(function () {
                    if(solicitud.telefono!=null){
          window.open ('https://api.whatsapp.com/send?phone=591'+solicitud.telefono+'&text=Hola%20'+nombre+',%20nos%20comunicamos%20de%20la%20Universidad%20Nacional%20del%20Oriente%20sede%20'+localStorage.getItem("Subsede") +',%20%20para%20informarte%20que%20tu%20solicitud%20por%20el%20libro%20"'+solicitud.titulo+'"%20a%20sido%20aprobada,%20ya%20puedes%20pasar%20a%20recoger%20el%20libro.'); 
          }
        })
        .then(function () {
          location.reload();
          
        })

                  .catch(function (error) {
                    console.log(error);
                  
                  });

          }
        })
        .catch(console.log);
  
          
    }
        )}

        catch( error) {
          console.log(error);
          // console.log(this.libro);
        }
    },


    devolverLibro(solicitud) {
      var estado1=0;
       if(solicitud.estado==3){
        estado1=4
       }
       if(solicitud.estado==4 || solicitud.estado==5){
        estado1=3
       }
      axios
        .put(RUTA_SERVIDOR_LIBROS+"/prestamos/" + solicitud.id, {
          idEmpleado: solicitud.idEmpleado,
          codigoUsuario: solicitud.codigoUsuario,
          idLibro: solicitud.idLibro,
          cantidad: solicitud.cantidad,
          idSubsede:solicitud.idSubsede,
          fechaPrestamo: solicitud.fechaPrestamo,
          fechaDevolucion: solicitud.fechaDevolucion,
          observacion: "Libro devulto",
          estado: estado1,
          activo: 1,
          telefono:solicitud.telefono,
        })
        .then(function () {
          if(estado1==3){
            
            fetch(RUTA_SERVIDOR_LIBROS+"/libros/"+ solicitud.idLibro)
        .then((respuesta) => respuesta.json())
        .then((datosRespuesta) => {
          
            axios
                  .put(RUTA_SERVIDOR_LIBROS+"/libros/" + solicitud.idLibro, {
                    titulo:datosRespuesta.titulo,
                    copias: datosRespuesta.copias,
                    copiasDisponibles: datosRespuesta.copiasDisponibles + 1,
                    categoria: datosRespuesta.categoria,
                    edicion: datosRespuesta.edicion,
                    codigo: datosRespuesta.codigo,
                    idAutor: parseInt(datosRespuesta.idAutor),
                    idEmpleado: datosRespuesta.idEmpleado,
                    idEditorial: parseInt(datosRespuesta.idEditorial),
                    idCarrera: parseInt(datosRespuesta.idCarrera),
                    urlPortada: datosRespuesta.urlPortada,
                    observacion: datosRespuesta.observacion,
                    activo: 1,
                  })
            
        .then(function () {
          location.reload();
          
        })

                  .catch(function (error) {
                    console.log(error);
                  
                  });

          
        })
        .catch(console.log);
  
          
          }else{location.reload();}
        })
        .catch(function (error) {
          console.log(error);
          // console.log(this.libro);
        });
    },

    entregarLibro(solicitud,) {
  
     try {
       var estado1=0;
       if(solicitud.estado==4){
        estado1=2
       }
       if(solicitud.estado==2){
        estado1=4
       }
      axios
        .put(RUTA_SERVIDOR_LIBROS+"/prestamos/" + solicitud.id, {
          idEmpleado: solicitud.idEmpleado,
          codigoUsuario: solicitud.codigoUsuario,
          idLibro: solicitud.idLibro,
          cantidad: solicitud.cantidad,
          idSubsede:solicitud.idSubsede,
          fechaPrestamo: solicitud.fechaPrestamo,
          fechaDevolucion: solicitud.fechaDevolucion,
          observacion: "libro entregado por el bibliotecario",
          estado: estado1,
          activo: 1,
          telefono:solicitud.telefono,
        })
        .then(function () {
          location.reload();
          
        })
    }
        catch( error) {
          console.log(error);
          // console.log(this.libro);
        }
    },
    imprimirSolicitudes(){
      //solicitud en pdf 
      var doc = new jsPDF();
      var elem = document.getElementById("solicitudesTabla");
      var res = doc.autoTableHtmlToJson(elem);
      doc.autoTable(res.columns, res.data, {
        theme: 'grid',
        styles: {
          overflow: 'linebreak',
          fontSize: 10,
          rowHeight: 20,
          font: 'helvetica',
          fontStyle: 'normal',
          lineWidth: 0.1,
          lineColor: [0.5, 0.5, 0.5],
          fillColor: [255, 255, 255],
          textColor: 0,
          halign: 'left',
          valign: 'middle',
          cellPadding: 2,
          cellWidth: 'auto',
          cellHeight: 'auto',
          tableWidth: 'auto',
          tableHeight: 'auto',
          tablePadding: 5,
          tableBorder: 1,
          tableBorderColor: [0, 0, 0],
          tableBackgroundColor: [255, 255, 255],
          headerBorder: 0,
          headerBorderColor: [0, 0, 0],
          headerBackgroundColor: [255, 255, 255],
          headerTextColor: 0,
          headerCellPadding: 5,
          headerCellWidth: 'auto',
          headerCellHeight: 'auto',
          headerFont: 'helvetica',
          headerFontSize: 10,
          headerFontStyle: 'bold',
          headerAlign: 'center',
          footerBorder: 0,
          footerBorderColor: [0, 0, 0],
          footerBackgroundColor: [255, 255, 255],
          footerTextColor: 0,
          footerCellPadding: 5,
          footerCellWidth: 'auto',
          footerCellHeight: 'auto',
          footerFont: 'helvetica',
          footerFontSize: 10,
          footerFontStyle: 'bold',
          footerAlign: 'center',
          columnWidth: 'auto',
          columnHeight: 'auto',
          columnPadding: 5,
          columnBorder: 1,
          columnBorderColor: [0, 0, 0],
          columnBackgroundColor: [255, 255, 255],
          columnTextColor: 0,
          columnAlign: 'left'
        },
        margin: {
          top: 10,
          bottom: 10,
          left: 10,
          right: 10
        }
      });
      doc.save('solicitudes.pdf');
    }
    
  }
  
};
</script>

<style lang="scss" scoped></style>
