<template>
  <div class="container row" ref="accionEditar">
    
    <form v-on:submit.prevent="agregarLibro" class="col-sm-12">
      <div class="row align-items-center">
        <div class="form-group col-sm">
          <label for="titulo">Titulo</label>
          <input
            required
            type="text"
            name="titulo"
            v-model="libro.titulo"
            id="titulo"
            class="form-control"
            placeholder="Titulo del libro"
            aria-describedby="titulo"
          />
        </div>

        <div class="form-group col-sm">
          <label for="autor">Autor</label>
          <select
            required
            class="form-select"
            v-model="libro.idAutor"
            aria-label="Default select example"
            id="autor"
            @change="obterAutor()"
          >
            <option v-for="nautor in autor" :key="nautor.id" :value="nautor.id">
              {{ nautor.nombre }}
            </option>
          </select>
        </div>

        <div class="form-group col-sm">
          <!-- <div class="form-group">
                      <label for="autor">Autor</label> 
                      <input v-model="libro.idAutor" type="text" required
                        class="form-control" name="autor" id="autor" aria-describedby="helpId" placeholder="selecciona el autor"  list="my-list-id">
                      
                      <datalist required  id="my-list-id" >
                    <option  required v-for="nautor in autor " :key="nautor.id" >{{nautor.nombre}}</option>
                    </datalist>
                    </div> -->
          <label for="">Carrera</label>
          <select
            v-model="libro.idCarrera"
            required
            class="form-select"
            name="carrera"
            id="carrera"
          >
            <option
              v-for="carrera in carreras"
              :key="carrera.id"
              :value="carrera.id"
            >
              {{ carrera.nombreCarrera }}
            </option>
          </select>
        </div>
      </div>

      <div class="row align-items-center">
        <div class="form-group col-sm">
          <label for="editorial">Editorial</label>
          <select
            required
            class="form-select"
            v-model="libro.idEditorial"
            aria-label="Default select example"
          >
            <option
              v-for="editorial in editoriales"
              :key="editorial.id"
              :value="editorial.id"
            >
              {{ editorial.nombreEditorial }}
            </option>
          </select>
        </div>

        <div class="form-group col-sm">
          <label for="edicion">Edicion</label>
          <input
            required
            type="number"
            name="edicion"
            v-model="libro.edicion"
            id="edicion"
            class="form-control"
            placeholder="edicion del libro"
            aria-describedby="edicion"
          />
        </div>

        <div class="form-group col-sm">
          <label for="categoria">Categoria</label>
          <input
            required
            type="text"
            name="categoria"
            v-model="libro.categoria"
            id="categoria"
            class="form-control"
            placeholder="categoria del libro"
            aria-describedby="categoria"
          />
        </div>
      </div>

      <div class="row align-items-center">
        <div class="col-sm">
          <label for="codigo">Código</label>
          <input
            required
            type="text"
            name="codigo"
            v-model="libro.codigo"
            id="codigo"
            class="form-control"
            placeholder="codigo del libro"
            aria-describedby="codigo"
          />
        </div>

        <div class="col-sm">
          <label for="copias">Cópias</label>
          <input
            required
            type="number"
            name="copias"
            v-model="libro.copias"
            id="copias"
            class="form-control"
            placeholder="cantidad de copias"
            aria-describedby="copias"
          />
        </div>

        <div class="col-sm">
          <label for="copias">Cópias disponibles</label>
          <input
            required
            type="number"
            name="copias"
            v-model="libro.copiasDisponibles"
            id="copias"
            class="form-control"
            placeholder="cantidad de copias"
            aria-describedby="copias"
          />
        </div>
      </div>

      <div class="form-group">
        <div class="col-sm">
          <label for="copias">Url portada</label>
          <input
            required
            type="text"
            name="copias"
            v-model="libro.urlPortada"
            id="url"
            class="form-control"
            placeholder="Imagen portada"
            aria-describedby="url"
          />
        </div>

<div class="col-sm">
          <label for="pdf">agraga un documento pdf</label>
          
          <input
            
            type="file"
            name="pdf"
            enctype="multipart/form-data"
            
            @change="buscarArchivo"
            accept="application/pdf"
          />
        </div>

        <label for="observacion">Observacion</label>
        <textarea
          required
          class="form-control"
          id="observacion"
          v-model="libro.observacion"
          rows="4"
          placeholder="Observacion del libro"
          aria-describedby="observacion"
        ></textarea>
      </div>
      <br />

      <div class="container row" raria-label="">
        <a
          v-if="variable < 0"
          v-on:click.stop="agregarLibro()"
          class="btn btn-success col-5"
        >
          Agregar nuevo libro
        </a>
        <a v-else v-on:click.stop="actualizar()" class="btn btn-success col-5">
          Actualizar libro
        </a>
        <span class="col-2"></span>
        <a
          v-if="variable < 0"
          v-on:click.stop="reset()"
          class="btn btn-danger col-5"
          >Resetear Formulario</a
        >
        <a v-on:click.stop="volver()" class="btn btn-danger col-5"
          >Cancelar edicion</a
        >
      </div>
    </form>

    
  </div>
</template>

<script>
import axios from "axios";
export const RUTA_SERVIDOR_ALUMNOS = process.env.VUE_APP_RUTA_API_ALUMNOS;
export const RUTA_SERVIDOR_LIBROS = process.env.VUE_APP_RUTA_API_LIBROS;

export default {
  data() {
    return {
      nombreEditorial: "",
      pdf:'',
      carreras: [],
      autor: [],
      libro: {
        titulo: "",
        copias: "",
        copiasDisponibles: "",
        categoria: "",
        edicion: "",
        codigo: "",
        observacion: "",
        idAutor: 1,
        idEmpleado: "",
        idSubsede: 0,
        urlPortada: "",
        idCarrera: 1,
        idEditorial: "",
        activo: 1,
      },

      nautor: {
        nombre: "",
        id: 0,
      },
      editoriales: [
        {
          nombreEditorial: "",
        },
      ],
      preNombre: "",
    };
  },
  props: {
    variable: {
      type: Number,
      default: 0,
    },
  },

  beforeMount() {
    this.consultarEditorial();
    this.consultarAutor();
    this.consultarCarreras();
    this.consultarLibro();
  },

  methods: {
    consultarLibro() {
      if (this.variable > 0) {
        fetch(RUTA_SERVIDOR_LIBROS+"/libros/" + this.variable)
          .then((respuesta) => respuesta.json())
          .then((datosRespuesta) => {
            this.libro = [];
            this.libro.id = datosRespuesta.id;
            this.libro.titulo = datosRespuesta.titulo;
            this.libro.copias = datosRespuesta.copias;
            this.libro.copiasDisponibles = datosRespuesta.copiasDisponibles;
            this.libro.categoria = datosRespuesta.categoria;
            this.libro.edicion = datosRespuesta.edicion;
            this.libro.codigo = datosRespuesta.codigo;
            this.libro.observacion = datosRespuesta.observacion;
            this.libro.idAutor = datosRespuesta.idAutor;
            this.libro.idEmpleado = datosRespuesta.idEmpleado;
            this.libro.idCarrera = datosRespuesta.idCarrera;
            this.libro.urlPortada = datosRespuesta.urlPortada;
            this.libro.idEditorial = datosRespuesta.idEditorial;
            this.libro.activo = datosRespuesta.activo;
            this.obterAutor();
          })
          .catch(console.log);
      } else {
        this.consultarEditorial();
        this.consultarAutor();
        this.consultarCarreras();
      }
    },

    agregarLibro() {
      let formData = new FormData();
      formData.append("titulo", this.libro.titulo);
      formData.append("copias", this.libro.copias);
      formData.append("copiasDisponibles", this.libro.copiasDisponibles);
      formData.append("categoria", this.libro.categoria);
      formData.append("edicion", this.libro.edicion);
      formData.append("codigo", this.libro.codigo);
      formData.append("observacion", this.libro.observacion);
      formData.append("idAutor", this.libro.idAutor);
      formData.append("idEmpleado", localStorage.getItem('id'));
      formData.append("idCarrera", this.libro.idCarrera);
      formData.append("urlPortada", this.libro.urlPortada);
      formData.append("idEditorial", this.libro.idEditorial);
      formData.append("activo", this.libro.activo);
      formData.append("documento", this.pdf);
      formData.append("idSubsede", localStorage.getItem("idSubsede"));
      axios
        .post(RUTA_SERVIDOR_LIBROS+"/libros/", formData, {
          headers: {
            "Content-Type": "multipart/form-data",
          },
        })
        .then(function (response) {
          console.log(response);
          location.reload();
        })
        .catch(function (error) {
          console.log(error);
        });
      
    },

    actualizar() {
      
      axios
        .put(RUTA_SERVIDOR_LIBROS+"/libros/" + this.libro.id, {
          titulo: this.libro.titulo,
          copias: this.libro.copias,
          copiasDisponibles: this.libro.copiasDisponibles,
          categoria: this.libro.categoria,
          edicion: this.libro.edicion,
          codigo: this.libro.codigo,
          idAutor: parseInt(this.libro.idAutor),
          idEmpleado: this.libro.idEmpleado,
          idSubsede: localStorage.getItem("idSubsede"),
          idEditorial: parseInt(this.libro.idEditorial),
          idCarrera: parseInt(this.libro.idCarrera),
          urlPortada: this.libro.urlPortada,
          observacion: this.libro.observacion,
          activo: 1,
        })
        .then(function (response) {
          console.log(Request);
          console.log(response);
          location.reload();
        })
        .catch(function (error) {
          console.log(error);
          // console.log(this.libro);
        });
    },

    consultarAutor() {
      fetch(RUTA_SERVIDOR_LIBROS+"/autor/")
        .then((respuesta) => respuesta.json())
        .then((datosRespuesta) => {
          if (typeof datosRespuesta[0].success === "undefined") {
            datosRespuesta.sort((a, b) => {
              if (a.nombre.toUpperCase() == b.nombre.toUpperCase()) {
                return 0;
              }
              if (a.nombre.toUpperCase() > b.nombre.toUpperCase()) {
                return 1;
              }
              if (a.nombre.toUpperCase() < b.nombre.toUpperCase()) {
                return -1;
              }
            });
            this.autor = datosRespuesta;
            this.libro.idAutor = this.autor[0].id;
            this.libro.idEditorial = this.editoriales[0].id;
          }
        })
        .catch(console.log);
    },
    consultarCarreras() {
      fetch(RUTA_SERVIDOR_LIBROS+"/carreras")
        .then((respuesta) => respuesta.json())
        .then((datosRespuesta) => {
          this.carreras = [];
          if (typeof datosRespuesta[0].success === "undefined") {
            this.carreras = datosRespuesta;
          }
        })
        .catch(console.log);
    },
    consultarEditorial() {
      fetch(RUTA_SERVIDOR_LIBROS+"/editoriales/")
        .then((respuesta) => respuesta.json())
        .then((datosRespuesta) => {
          if (typeof datosRespuesta[0].success === "undefined") {
            datosRespuesta.sort((a, b) => {
              if (
                a.nombreEditorial.toUpperCase() ==
                b.nombreEditorial.toUpperCase()
              ) {
                return 0;
              }
              if (
                a.nombreEditorial.toUpperCase() >
                b.nombreEditorial.toUpperCase()
              ) {
                return 1;
              }
              if (
                a.nombreEditorial.toUpperCase() <
                b.nombreEditorial.toUpperCase()
              ) {
                return -1;
              }
            });
            this.editoriales = datosRespuesta;
          }
        })
        .catch(console.log);
    },

    reset() {
      Object.assign(this.$data, this.$options.data());
      this.consultarEditorial();
      this.consultarAutor();
      this.consultarCarreras();
    },
    volver() {
      location.reload();
    },
    obterAutor() {
      this.preNombre = this.autor.find(
        (x) => x.id == this.libro.idAutor
      ).nombre;
    },
    Previa() {
      let index = 0;
      while (index != this.libro.idAutor) {
        if (this.libro.idAutor == this.autor[index].id) {
          this.preNombre = this.autor[index].nombre;
          index = this.libro.idAutor;
        } else {
          index++;
        }
      }
    },

    buscarArchivo(event){
      this.pdf= event.target.files[0];
      
    }



  },
};
</script>
