      </div>
      
        <div v-else>
<div class="icon-container-2" 
        v-bind:style="{backgroundImage: 'url(' + libro.urlPortada + ')', backgroundSize: 'cover'}"
        @click="
                redireccionaarPdf(
                  libro.documento
                )"
        >
          <div v-if='acceso==2'>
          <div class="btn-libro-1">
            <div align="center" class="btn-libro" @click="Enviar(libro.id)">
                ✍🏻
            </div>
          </div>
          <div class="btn-circle-2">
            <div v-on:click="eliminar(libro)" class="btn-circle">
             ❌
            </div>
          </div>
          </div>
        </div>
        </div>

        <div class="titulo">{{ libro.titulo }}</div>
        <div class="autor" >
           
              <img
                src="https://img.icons8.com/ios-filled/18/000000/guest-male--v1.png"
              />
              {{ libro.nombre }}
               <div style="font-size: 100%">
            
            <div class="editorial" style="font-size: 100%">
           
            </div>
          </div>
            </div>
      </div>
    </div>
    </div>



    <div class="estudiante" v-if='acceso==2'>


    <div class="d-none d-sm-none d-md-block table-responsive table-sm small border" >
          <table class="table text-center table-striped" id="">
            <thead>
              <tr>
                <!-- <th>ID</th> -->
                <th>TITULO</th>
                <th>AUTOR</th>
                <th>EDITORIAL</th>
                <th>EDICION</th>
                <th>CODIGO</th>
                <th>COPIAS</th>
                <th>DISPONIBLES</th>
                <th>ACCION</th>
                
              </tr>
            </thead>
            <tbody>
              <tr v-for="libro in datosPaginados" :key="libro.id">
                <td>{{ libro.titulo}}</td>
                <td>{{  libro.nombre}}</td>
                 <td>{{editoriales[[libro.idEditorial] - 1].nombreEditorial}} </td>
                  <td>{{  libro.edicion}}</td>
                   <td>{{  libro.codigo}}</td>
                    <td>{{  libro.copias}}</td>
                    <td>{{  libro.copiasDisponibles}}</td>

                <td>
                  <div class="btn-group" role="group" aria-label="">
                  <div v-if="libro.documento!=null">
                    <button @click="redireccionaarPdf(libro.documento)"
                     class="btn btn-info">
                      Ver PDF
                    </button>
                    </div>
                  <div v-else>
                    <button
                     v-b-modal.modal-lg
                     @click="
                obtenerLibro(
                  libro.id,
                  libro.titulo,
                  libro.nombre,
                  editoriales[[libro.idEditorial] - 1].nombreEditorial,
                  libro.edicion,
                  libro.urlPortada,
                )"
                      class="btn btn-info"
                    >
                      📚
                    </button>
                    </div>
                  
                  
                   <button
                      v-b-modal.modal-center
                     @click="Enviar(libro.id)"
                      class="btn btn-success"
                    >
                      ✍🏻
                    </button>
                   
                    
                    <button
                      type="button"
                      @click="eliminar(libro)"
                      class="btn btn-danger"
                    >
                      𝗫
                    </button>
                     </div>
                    
                </td> 
              </tr>
            </tbody>
          </table>
        </div>




    </div>






    <nav aria-label="Page navigation example">
      <ul align="center" class="pagination">
        <li class="page-item" v-on:click="paginaPrevia()">
          <a class="page-link" href="#">Previous</a>
        </li>
        <li
          v-for="pagina in totalPaginas()"
          :key="pagina"
          class="page-item"
          v-bind:class="activo(pagina)"
        >
          <a class="page-link" href="#" v-on:click="ObtenerDatosPag(pagina)">{{
            pagina
          }}</a>
        </li>
        <li class="page-item" v-on:click="paginaSiguiente()">
          <a class="page-link" href="#">Next</a>
        </li>
      </ul>
    </nav>
    <b-modal id="modal-lg" size="lg" centered title="Solicitud de prestamo" hide-footer>
      <modalPrestamo v-bind:Libro="this.prestamolibro"></modalPrestamo>
    </b-modal>
  
  
  
  




 <div class="estudiante" v-if='acceso==2' style="display:none;">


    <div class="d-none d-sm-none d-md-block table-responsive table-sm small " >
        
          <table class="table" id="tablaLibros">
            <thead>
              <tr>
                <th>ID</th>
                <th>TITULO</th>
                <th>AUTOR</th>
                <th>EDITORIAL</th>
                <th>EDICION</th>
                <th>CODIGO</th>
                <th>COPIAS</th>
                <th>DISPONIBLES</th>
               
                
              </tr>
            </thead>
            <tbody>
              <tr v-for="libro , index in dataToExport" :key="libro.id">
                <td>{{index+1}}</td>
                <td>{{ libro.titulo}}</td>
                <td>{{  libro.nombre}}</td>
                 <td>{{editoriales[[libro.idEditorial] - 1].nombreEditorial}} </td>
                  <td>{{  libro.edicion}}</td>
                   <td>{{  libro.codigo}}</td>
                    <td>{{  libro.copias}}</td>
                    <td>{{  libro.copiasDisponibles}}</td>

                
              </tr>
            </tbody>
          </table>
        </div>




    </div>


  

 <div class="estudiante" v-if='acceso==2' >

  <button @click="exportExcel()">Export table to excel xls</button>
   <button @click="exportPdf()">Export table pdf</button>
</div>
  
  
  </div>

</template>

<script>

// import * as XLSX from 'xlsx'
import * as saveAs from 'file-saver';
import listar from '@/views/LibrosView.vue'
import { jsPDF, } from 'jspdf';
import  'jspdf-autotable';
// import { Carousel, Slide } from 'vue-carousel';
import modalPrestamo from "@/components/modalPrestamo.vue";
import axios from "axios";
// import CrearLibro from '@/components/CrearLibro.vue'
export const RUTA_SERVIDOR_ALUMNOS = process.env.VUE_APP_RUTA_API_ALUMNOS;
export const RUTA_SERVIDOR_LIBROS = process.env.VUE_APP_RUTA_API_LIBROS;

export default {
  name: "ListarLibro",

  components: {
    modalPrestamo,
    // CrearLibro,
    // Carousel,
    // Slide
  },

  data() {
    
    return {
      dataToExport: [],
      prestamolibro: {
        id: 0,
        nombreLibro: "",
        autor: "",
        editorial: "",
        edicion: "",
        urlPortada: "",
      },
      subsede:localStorage.getItem("idSubsede"),
      totalLibros: [],
      libros: [],
      autor: [],
      carreras: [],
      materia: 1,
      nombreLibro: "",
      nombreAutor: 1,
      editoriales: [],
      elementosPorPagina: 12,
      datosPaginados: [],
      paginaActual: 1,
      acceso: localStorage.getItem("acceso"),

      radioPdf:0,
     
    };
  },

  created: function () {
    this.consultarAutor();
    this.consultarEditorial();
    

    this.consultarCarreras();

    this.consultarLibros();
 
  },
  mounted() {
   
    this.ObtenerDatosPag(1);
  },
  
  methods: {

    redireccionaarPdf(pdf){
      var a = document.createElement("a");
        a.target = "_blank";
        a.href="http://192.168.0.113:8082/storage/documentos/"+pdf;
        a.click();
    },

    
    
    consultarLibros() {
      fetch(RUTA_SERVIDOR_LIBROS+"/librosSubsede/"+ this.subsede)
        .then((respuesta) => respuesta.json())
        .then((datosRespuesta) => {
          
          this.libros = [];
          if (typeof datosRespuesta[0].success === "undefined") {
            datosRespuesta.sort((a, b) => {
              if (a.titulo.toUpperCase() == b.titulo.toUpperCase()) {
                return 0;
              }
              if (a.titulo.toUpperCase() > b.titulo.toUpperCase()) {
                return 1;
              }
              if (a.titulo.toUpperCase() < b.titulo.toUpperCase()) {
                return -1;
              }
            });
            this.libros = datosRespuesta;

             for (let index = 0; index < this.libros.length; index++) {
            for (let index2 = 0; index2 < this.autor.length; index2++) {
          if(this.autor[index2].id==this.libros[index].idAutor){
          var nombreAutor = this.autor[index2].nombre;
        this.libros[index].nombre=nombreAutor;
         
          }
        }
             }
            this.filtrarlibros();
            this.filtrarArchivos();
          }
        })
        .catch(console.log);
    },

    
    filtrarArchivos(){
      this.dataToExport = [];
      for (let index = 0; index < this.libros.length; index++) {
        if ( this.libros[index].idCarrera === this.materia && this.libros[index].activo === 1 && (this.libros[index].documento!=null && this.radioPdf==1)
        )         
        {
          this.dataToExport = this.dataToExport.concat(this.libros[index]);
        }
        else{
          if (
          this.libros[index].idCarrera === this.materia &&
          this.libros[index].activo === 1 && (this.libros[index].documento==null && this.radioPdf==0)     )   
        {
          this.dataToExport = this.dataToExport.concat(this.libros[index]);
        }
        }
      }
  
    },


    filtrarlibros() {
      this.totalLibros = [];
      for (let index = 0; index < this.libros.length; index++) {
        if (
          this.libros[index].idCarrera === this.materia &&
          this.libros[index].activo === 1 &&
          (this.libros[index].titulo.toUpperCase().includes(this.nombreLibro.toUpperCase()) ||
            this.libros[index].nombre.toUpperCase().includes(this.nombreLibro.toUpperCase()))
            &&(this.libros[index].documento!=null && this.radioPdf==1)
        )         
        {
          this.totalLibros = this.totalLibros.concat(this.libros[index]);
          this.ObtenerDatosPag(1);
        }
        else{
          if (
          this.libros[index].idCarrera === this.materia &&
          this.libros[index].activo === 1 &&
          (this.libros[index].titulo
            .toUpperCase()
            .includes(this.nombreLibro.toUpperCase()) ||
            this.libros[index].nombre
              .toUpperCase()
              .includes(this.nombreLibro.toUpperCase()))&&
              (this.libros[index].documento==null && this.radioPdf==0)
        )         
        {
          this.totalLibros = this.totalLibros.concat(this.libros[index]);
          this.ObtenerDatosPag(1);
        }
        }
        
      }
      if (this.totalLibros.length === 0) {
        this.paginaPrevia();
        //this.paginaActual=0;
      }
    },

    consultarAutor() {
      fetch(RUTA_SERVIDOR_LIBROS+"/autor/")
        .then((respuesta) => respuesta.json())
        .then((datosRespuesta) => {
          
            this.autor = datosRespuesta;
          
          
        })
        .catch(console.log);
    },

    consultarEditorial() {
      fetch(RUTA_SERVIDOR_LIBROS+"/editoriales/")
        .then((respuesta) => respuesta.json())
        .then((datosRespuesta) => {
          if (typeof datosRespuesta[0].success === "undefined") {
            this.editoriales = datosRespuesta;
          }
        })
        .catch(console.log);
    },

    Enviar(id) {
      this.$emit("recibiendo", id);
      listar.editar();
    
    },

    eliminar(libro) {
      axios
        .put(RUTA_SERVIDOR_LIBROS+"/libros/" + libro.id, {
          titulo: libro.titulo,
          copias: libro.copias,
          copiasDisponibles: libro.copiasDisponibles,
          categoria: libro.categoria,
          edicion: libro.edicion,
          codigo: libro.codigo,
          idAutor: parseInt(libro.idAutor),
          idEmpleado: libro.idEmpleado,
          idEditorial: parseInt(libro.idEditorial),
          idCarrera: parseInt(libro.idCarrera),
          urlPortada: libro.urlPortada,
          observacion: libro.observacion,
          documento: libro.documento,
          activo: 0,
        })
        .then(function (response) {
        
          location.reload();
        })
        .catch(function (error) {
          console.log(error);
          // console.log(this.libro);
        });
    },
    obtenerLibro(id, nombre, autor, editorial, edicion,urlPortada) {
      this.prestamolibro = {
        id: id,
        nombreLibro: nombre,
        autor: autor,
        editorial: editorial,
        edicion: edicion,
        urlPortada:urlPortada,
      };
    },

    consultarCarreras() {
      fetch(RUTA_SERVIDOR_LIBROS+"/carreras")
        .then((respuesta) => respuesta.json())
        .then((datosRespuesta) => {
          this.carreras = [];
          if (typeof datosRespuesta[0].success === "undefined") {
            this.carreras = datosRespuesta;
          }
        })
        .catch(console.log);
    },

    totalPaginas() {
      return Math.ceil(this.totalLibros.length / this.elementosPorPagina);
    },
    ObtenerDatosPag(noPag) {
      this.paginaActual = noPag;
      this.datosPaginados = [];
      let ini = noPag * this.elementosPorPagina - this.elementosPorPagina;
      let fin = noPag * this.elementosPorPagina;
      this.datosPaginados = this.totalLibros.slice(ini, fin);
     
    },
    paginaPrevia() {
      if (this.paginaActual > 1) {
        this.paginaActual--;
      }
      this.ObtenerDatosPag(this.paginaActual);
    },
    paginaSiguiente() {
      if (this.paginaActual < this.totalPaginas()) {
        this.paginaActual++;
      }
      this.ObtenerDatosPag(this.paginaActual);
    },
    activo(noPoag) {
      return noPoag === this.paginaActual ? "active" : "";
    },
    precionadoBoton(id) {
      console.log(id);
    },
    cambiaC(carrera) {
      this.materia = carrera;
      this.filtrarlibros();
      this.filtrarArchivos();
    },

    exportExcel(){
      //convertir tabla html a excel

      var htmlTable = document.getElementById('tablaLibros');
      var html = htmlTable.outerHTML;
      var blob = new Blob([html], {
        type: "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=utf-8"
      });
      saveAs(blob, "Libros.xls");

    },

    exportPdf(){
      //convertir tabla en pdf sin JSPDF
      var doc = new jsPDF('p', 'pt');
      var elem = document.getElementById("tablaLibros");
      var res = doc.autoTableHtmlToJson(elem);
      doc.autoTable(res.columns, res.data, {
        theme: 'grid',
        styles: {
          overflow: 'linebreak',
          fontSize: 8,
          rowHeight: 20,
          font: 'helvetica',
          fontStyle: 'normal',
          lineWidth: 0.1,
          lineColor: [0, 0, 0],
          fillColor: [255, 255, 255],
          textColor: [0, 0, 0],
          halign: 'left',
          valign: 'middle',
          columnStyles: {
            0: { columnWidth: 'auto' },
            1: { columnWidth: 'auto' },
            2: { columnWidth: 'auto' },
            3: { columnWidth: 'auto' },
            4: { columnWidth: 'auto' },
            5: { columnWidth: 'auto' },
            6: { columnWidth: 'auto' },
            7: { columnWidth: 'auto' },
            8: { columnWidth: 'auto' },
            9: { columnWidth: 'auto' },
            10: { columnWidth: 'auto' },
            11: { columnWidth: 'auto' },
            12: { columnWidth: 'auto' },
            13: { columnWidth: 'auto' },
            14: { columnWidth: 'auto' },
            15: { columnWidth: 'auto' },
            16: { columnWidth: 'auto' },
            17: { columnWidth: 'auto' },
            18: { columnWidth: 'auto' },
            19: { columnWidth: 'auto' },
            20: { columnWidth: 'auto' },
            21: { columnWidth: 'auto' },
            22: { columnWidth: 'auto' },
            23: { columnWidth: 'auto' },
            24: { columnWidth: 'auto' },
            25: { columnWidth: 'auto' },
            26: { columnWidth: 'auto' },
            27: { columnWidth: 'auto' },
            28: { columnWidth: 'auto' },
            29: { columnWidth: 'auto' },
            30: { columnWidth: 'auto' },
          },
        },
        margin: {
          top: 80,
          left: 20,
          right: 20,
          bottom: 20,
        },
        
      });
      
      doc.save("Libros.pdf");
    },
   

  },
};
</script>

