<template class="fondo">
  <div class="row align-items-center">
    <div class="col">
      <div class="container align-items-center">
        <div class="form-group text-center">
          <label for="">Ingresa el nombre del autor</label>
          <input
            v-on:keyup="filtrarAutores()"
            v-model="nombreAutor"
            type="text"
            class="form-control text-center"
            placeholder="Nombre del autor"
          />
        </div>
<br>
        <div class="d-none d-sm-none d-md-block  table-responsive table-sm small">
          <table class="table table-striped border text-center">
            <thead>
              <tr>
                <!-- <th>ID</th> -->
                <th>NOMBRE</th>
                <th>ACCIONES</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="autor in datosPaginados" :key="autor.id">
                <!-- <td>{{ libro.id }}</td> -->
                <td>{{ autor.nombre }}</td>
                <!-- <td>{{ libro.codigo }}</td>
                    <td>{{ libro.copias }}</td>
                    <td>{{ libro.categoria }}</td>
                    <td>{{ libro.edicion }}</td>
                    <td>{{ autor[[libro.idAutor] - 1].nombre }}</td>
                    <td>{{ editoriales[[libro.idEditorial] - 1].nombreEditorial }}</td>
                    <td>{{ carreras[[libro.idCarrera] - 1].nombreCarrera }}</td>
                    <td>{{ libro.observacion }}</td> -->
                <td>
                  <div class="btn-group" role="group" aria-label="">
                    <button
                      v-b-modal.modal-center
                      @click="obtenerAutor(autor)"
                      class="btn btn-info btn-sm"
                    >
                      Editar
                    </button>
                    <button
                      type="button"
                      @click="eliminar(autor)"
                      class="btn btn-danger btn-sm"
                    >
                      Eliminar
                    </button>
                  </div>
                </td>
              </tr>
            </tbody>
          </table>
        </div>

        <!-- <nav aria-label="Page navigation example">
            <ul class="pagination">
                <li class="page-item" v-on:click="paginaPrevia()">
                    <a class="page-link" href="#">Previous</a>
                </li>
                <li v-for="pagina in totalPaginas()" :key="pagina" class="page-item" v-bind:class="activo(pagina)">
                    <a class="page-link" href="#" v-on:click="ObtenerDatosPag(pagina)">{{pagina}}</a>
                </li>
                <li class="page-item" v-on:click="paginaSiguiente()">
                    <a class="page-link" href="#">Next</a>
                </li>
            </ul>
        </nav> -->
      </div>
    </div>

    <nav aria-label="Page navigation example">
      <ul class="pagination">
        <li class="page-item" v-on:click="paginaPrevia()">
          <a class="page-link" href="#">Previous</a>
        </li>
        <li
          v-for="paginaE in totalPaginas()"
          :key="paginaE"
          class="page-item"
          v-bind:class="activo(paginaE)"
        >
          <a class="page-link" href="#" v-on:click="ObtenerDatosPag(paginaE)">{{
            paginaE
          }}</a>
        </li>
        <li class="page-item" v-on:click="paginaSiguiente()">
          <a class="page-link" href="#">Next</a>
        </li>
      </ul>
    </nav>

    <b-modal id="modal-center" centered title="Modificar autor">
      <div class="form-group">
        <label for="">nombre autor</label>
        <input
          v-model="autorEditar.nombre"
          type="text"
          name=""
          id=""
          class="form-control"
          placeholder="autorEditar.nombre"
          aria-describedby="helpId"
        />
        <button @click="actualizar()"></button>
      </div>
    </b-modal>
  </div>
</template>

<script>
import axios from "axios";
export const RUTA_SERVIDOR_ALUMNOS = process.env.VUE_APP_RUTA_API_ALUMNOS;
export const RUTA_SERVIDOR_LIBROS = process.env.VUE_APP_RUTA_API_LIBROS;

export default {
  name: "ListarAutores",
  data() {
    return {
      totalAutores: [],
      nombreAutor: "",
      autores: [],
      elementosPorPagina: 10,
      datosPaginados: [],
      paginaActual: 1,
      autorEditar: {
        nombre: "",
        id: 0,
      },
    };
  },

  created: function () {
    this.consultarAutores();
  },
  mounted() {
    this.ObtenerDatosPag(1);
  },
  methods: {
    consultarAutores() {
      fetch(RUTA_SERVIDOR_LIBROS+"/autor")
        .then((respuesta) => respuesta.json())
        .then((datosRespuesta) => {
          this.libros = [];
          if (typeof datosRespuesta[0].success === "undefined") {
            datosRespuesta.sort((a, b) => {
              if (a.nombre.toUpperCase() == b.nombre.toUpperCase()) {
                return 0;
              }
              if (a.nombre.toUpperCase() > b.nombre.toUpperCase()) {
                return 1;
              }
              if (a.nombre.toUpperCase() < b.nombre.toUpperCase()) {
                return -1;
              }
            });
            this.autores = datosRespuesta;
            this.filtrarAutores();
          }
        })
        .catch(console.log);
    },

    filtrarAutores() {
      this.totalAutores = [];
      for (let index = 0; index < this.autores.length; index++) {
        if (
          this.autores[index].activo === 1 &&
          this.autores[index].nombre
            .toUpperCase()
            .includes(this.nombreAutor.toUpperCase())
        ) {
          this.totalAutores = this.totalAutores.concat(this.autores[index]);
          this.ObtenerDatosPag(1);
        }
      }
      if (this.totalAutores.length === 0) {
        this.paginaPrevia();
        //this.paginaActual=0;
      }
    },

    eliminar(autor) {
      console.log(autor);
      axios
        .put(RUTA_SERVIDOR_LIBROS+"/autor/" + autor.id, {
          nombre: autor.nombre,
          idEmpleado: autor.idEmpleado,
          activo: 0,
        })
        .then(function (response) {
          console.log(response);
          location.reload();
        })
        .catch(function (error) {
          console.log(error);
          // console.log(this.libro);
        });
    },

    totalPaginas() {
      return Math.ceil(this.totalAutores.length / this.elementosPorPagina);
    },

    ObtenerDatosPag(noPag) {
      this.paginaActual = noPag;
      this.datosPaginados = [];
      let ini = noPag * this.elementosPorPagina - this.elementosPorPagina;
      let fin = noPag * this.elementosPorPagina;
      this.datosPaginados = this.totalAutores.slice(ini, fin);
    },

    paginaPrevia() {
      if (this.paginaActual > 1) {
        this.paginaActual--;
      }
      this.ObtenerDatosPag(this.paginaActual);
    },

    paginaSiguiente() {
      if (this.paginaActual < this.totalPaginas()) {
        this.paginaActual++;
      }
      this.ObtenerDatosPag(this.paginaActual);
    },
    activo(noPoag) {
      return noPoag === this.paginaActual ? "active" : "";
    },

    obtenerAutor(idAutor) {
      this.autorEditar.nombre = idAutor.nombre;
      this.autorEditar.id = idAutor.id;
    },
    actualizar() {
      axios
        .put(RUTA_SERVIDOR_LIBROS+"/autor/" + this.autorEditar.id, {
          nombre: this.autorEditar.nombre.toUpperCase(),
          idEmpleado: parseInt(localStorage.getItem('id')),
          activo: 1,
        })
        .then(function (response) {
          console.log(response);
          location.reload();
        })
        .then(alert("El autor se actualizo correctamente"))
        .catch(function (error) {
          console.log(error);
          // console.log(this.libro);
        });
    },
  },
};
</script>
